.footer__author {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}

@media screen and (max-width: 900px) {
  .footer__author {
    font-size: 16px;
  }
}

@media screen and (max-width: 500px) {
  .footer__author {
    font-size: 14px;
    line-height: 17px;
  }
}
